import { useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

const App = () => {
    const now = new Date()
    const yyyy = now.getFullYear()
    let mm = now.getMonth()+1
    if(mm<10) mm = `0${mm}`
    let dd = now.getDate()
    if(dd<10) dd = `0${dd}`
    const [id, setId] = useState(''); // 
    const [serviceDate, setServiceDate] = useState(`${yyyy}-${mm}-${dd}`); // 
    const [serviceTime, setServiceTime] = useState(''); // 
    const [gender, setGender] = useState(''); // 
    const [name, setName] = useState(''); // 
    const [birthday, setBirthday] = useState(''); // 
    const [phone, setpPhone] = useState(''); // 
    const [serviceStatus, setServiceStatus] = useState(''); // 
    const [serviceContent, setServiceContent] = useState(null); // 
    const [livingArea, setLivingArea] = useState(''); //
    const [caseFamilySubsidy, setCaseFamilySubsidy] = useState(''); //
    const [employmentSituation, setEmploymentSituation] = useState(''); //
    const [causeOfFailure, setCauseOfFailure] = useState(''); //
    const [domicileaddress, setDomicileaddress] = useState(''); //
    const [idNumber, setIdNumber] = useState(''); //
    const [oldSystemCategory, setOldSystemCategory] = useState(''); //
    const [contactPersonName, setContactPersonName] = useState(''); //
    const [obstacleCategory, setObstacleCategory] = useState(''); //
    const [barrierLevel, setBarrierLevel] = useState(''); //
    const [ageGroup, setAgeGroup] = useState(''); //
    const [serviceObjectSource, setServiceObjectSource] = useState(''); //


    const handleChangeIdNumber = async e => {
        const inputValue = e.target.value
        setIdNumber(inputValue)


        const data = await axios
            .post('/api/form/search', {idNumber: inputValue})
            .then(res => res.data)
            .catch((err) => console.error(err));

        if(data.result) {
            console.log(data.data)
            let {data:{
                gender,
                name,
                birthday,
                phone,
                domicileaddress,
                contactPersonName
            }} = data

            birthday = birthday.substring(0,10)
            setGender(gender)
            setName(name)
            setBirthday(birthday)
            setpPhone(phone)
            setDomicileaddress(domicileaddress)
            setContactPersonName(contactPersonName)
        }
            

    }

    const handleSubmit = (e) => {
        e.preventDefault();

        if(gender === '') return alert('')

        const formValue = {
            id,
            serviceDate,
            serviceTime,
            gender,
            name,
            birthday,
            phone,
            serviceStatus,
            serviceContent,
            livingArea,
            caseFamilySubsidy,
            employmentSituation,
            causeOfFailure,
            domicileaddress,
            idNumber,
            oldSystemCategory,
            contactPersonName,
            obstacleCategory,
            barrierLevel,
            ageGroup,
            serviceObjectSource,
        };

        axios
            .post('/api/form', formValue)
            .then((res) => alert(''))
            .catch((err) =>{ 
                alert('')
                console.error(err)});

    };
    return (
        <Form onSubmit={handleSubmit}>
            <FormItem>
                <Label></Label>
                <Input
                    type="text"
                    placeholder=""
                    value={id}
                    onChange={(e) => setId(e.target.value)}
                />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input
                    type="date"
                    value={serviceDate}
                    onChange={(e) => setServiceDate(e.target.value)}
                    required
                />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input
                    type="text"
                    placeholder=""
                    value={serviceTime}
                    onChange={(e) => setServiceTime(e.target.value)}
                />
            </FormItem>
            <hr/>
            <FormItem>
                <Label></Label>
                <Input value={idNumber} onChange={handleChangeIdNumber} required />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select 
                value={gender}
                    onChange={(e) => setGender(e.target.value)}
                    >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input
                    type="text"
                    placeholder=""
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input
                    type="date"
                    placeholder=""
                    value={birthday}
                    onChange={(e) => setBirthday(e.target.value)}
                    required
                />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input
                    type="text"
                    placeholder=""
                    value={phone}
                    onChange={(e) => setpPhone(e.target.value)}
                    required
                />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input value={domicileaddress} onChange={e => setDomicileaddress(e.target.value)} required />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input value={contactPersonName} onChange={e => setContactPersonName(e.target.value)} />
            </FormItem>
            <hr />
            <FormItem>
                <Label></Label>
                <Select
                    value={serviceContent}
                    onChange={(e) => setServiceContent(e.target.value)}
                    
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={serviceStatus}
                    onChange={(e) => setServiceStatus(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value="()">
                        ()
                    </option>
                    <option value="()">
                        ()
                    </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={livingArea}
                    onChange={(e) => setLivingArea(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={caseFamilySubsidy}
                    onChange={(e) => setCaseFamilySubsidy(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={employmentSituation}
                    onChange={(e) => setEmploymentSituation(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={causeOfFailure}
                    onChange={(e) => setCauseOfFailure(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Input value={oldSystemCategory} onChange={e => setOldSystemCategory(e.target.value)} />
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={obstacleCategory}
                    onChange={(e) => setObstacleCategory(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={barrierLevel}
                    onChange={(e) => setBarrierLevel(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={ageGroup}
                    onChange={(e) => setAgeGroup(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value="0-3">0-3</option>
                    <option value="3-6">3-6</option>
                    <option value="6-12">6-12</option>
                    <option value="12-15">12-15</option>
                    <option value="15-18">15-18</option>
                    <option value="18-30">18-30</option>
                    <option value="30-45">30-45</option>
                    <option value="45-50">45-50</option>
                    <option value="50-60">50-60</option>
                    <option value="60-65">60-65</option>
                    <option value="65">65</option>
                    <option value=""></option>
                </Select>
            </FormItem>
            <FormItem>
                <Label></Label>
                <Select
                    value={serviceObjectSource}
                    onChange={(e) => setServiceObjectSource(e.target.value)}
                    required
                >
                    <option hidden></option>
                    <option value=""></option>
                    <option value=""></option>
                </Select>
            </FormItem>
            
            <button type="submit"></button>
        </Form>
    );
};

export default App;

